.main-navigation {
  position: relative;
  width: auto;
}
.main-navigation--opened {
  background-color: #283645;
  min-height: 255px;
}
@media (min-width: 1200px) {
  .main-navigation--opened {
    min-width: 618px;
    min-height: 90px;
    background-color: transparent;
  }
}
.main-navigation__toggle {
  position: absolute;
  top: -45px;
  right: 0;
  display: block;
  width: 50px;
  height: 25px;
  background-color: #283645;
  cursor: pointer;
  border: none;
}
@media (min-width: 660px) {
  .main-navigation__toggle {
    top: -68px;
  }
}
@media (min-width: 1200px) {
  .main-navigation__toggle {
    display: none;
  }
}
.main-navigation--closed .main-navigation__toggle {
  background-color: #1d2631;
}
.main-navigation--closed .main-navigation__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 19px;
  width: 50px;
  height: 3px;
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}
.main-navigation--closed .main-navigation__toggle:hover::before,
.main-navigation--closed .main-navigation__toggle:focus::before {
  background-color: #d22856;
  box-shadow: 0 8px 0 0 #d22856, 0 16px 0 0 #d22856;
}
.main-navigation--closed .main-navigation__toggle:active::before {
  opacity: 0.3;
}
@media (max-width: 1199px) {
  .main-navigation--closed .main-navigation__list {
    display: none;
  }
}
.main-navigation--opened .main-navigation__toggle::before,
.main-navigation--opened .main-navigation__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 0;
  width: 32px;
  height: 3px;
  background-color: #ffffff;
}
.main-navigation--opened .main-navigation__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-navigation--opened .main-navigation__toggle::after {
  transform: rotate(-45deg);
}
.main-navigation--opened .main-navigation__toggle:hover::before,
.main-navigation--opened .main-navigation__toggle:focus::before,
.main-navigation--opened .main-navigation__toggle:hover::after,
.main-navigation--opened .main-navigation__toggle:focus::after {
  background-color: #d22856;
}
.main-navigation--opened .main-navigation__toggle:active::before,
.main-navigation--opened .main-navigation__toggle:active::after {
  opacity: 0.3;
}
.main-navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  border-top: 1px solid #252527;
}
@media (min-width: 1200px) {
  .main-navigation__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    flex-wrap: wrap;
    flex-grow: 1;
